/* ---------- tokens ---------- */
:root{
  --border:#d8e3f5;
  --ink:#0f172a;
  --muted:#6b7280;
  --brand:#155acb;

  /* muted camo palette */
  --camo-base:#f4f7f2; /* base paper tone */
  --camo-1:#e8eee6;
  --camo-2:#e9efe9;
  --camo-3:#eceee7;
  --camo-4:#e7ebe2;
}

/* ---------- reset ---------- */
*{ box-sizing:border-box; }
html,body{ margin:0; padding:0; color:var(--ink); }

/* ---------- camo background (CSS-only, no JS) ---------- */
/* DO NOT set a body background elsewhere; this layer is the source of truth. */
body{
  background: var(--camo-base);
}

/* The camo is a fixed, behind-the-app layer. */
body::before{
  content:"";
  position:fixed;
  inset:0;
  z-index:-1;
  pointer-events:none;

  /* subtle organic blobs + faint linen texture */
  background:
    radial-gradient(1000px 700px at 8% 14%,  var(--camo-1) 0%, transparent 70%),
    radial-gradient(900px  600px at 85% 18%, var(--camo-2) 0%, transparent 68%),
    radial-gradient(1100px 760px at 18% 86%, var(--camo-3) 0%, transparent 72%),
    radial-gradient(900px  640px at 78% 78%, var(--camo-4) 0%, transparent 62%),
    /* linen-like stipple */
    repeating-linear-gradient(0deg,  rgba(0,0,0,.02) 0 2px, rgba(0,0,0,0) 2px 6px),
    repeating-linear-gradient(90deg, rgba(0,0,0,.02) 0 2px, rgba(0,0,0,0) 2px 6px),
    var(--camo-base);
  background-attachment: fixed,fixed,fixed,fixed,fixed,fixed,fixed;
  filter: saturate(.95) contrast(1.02);
}

/* ---------- your existing layout bits (transparent-friendly) ---------- */
a{ text-decoration:none; color:inherit; }

.site-header{
  position:sticky; top:0; z-index:50;
  background:rgba(255,255,255,.92);
  backdrop-filter:saturate(120%) blur(6px);
  border-bottom:1px solid var(--border);
}

.page-wrap{ max-width:980px; margin:16px auto; padding:12px; }
/* keep content cards white so camo peeks around edges */
.page-wrap > *{
  background:rgba(255,255,255,.94);
  border:1px solid rgba(216,227,245,.9);
  border-radius:12px;
  box-shadow:0 8px 30px rgba(15,23,42,.06);
  padding:12px;
}

input,select,textarea{
  border:1px solid var(--border); border-radius:8px; background:#fbfdff; padding:6px 8px;
}
label{ font-size:12px; font-weight:700; color:#334155; margin-bottom:4px; display:block; }

/* print clean */
@media print{
  @page{ size:Letter; margin:.5in; }
  .site-header{ display:none !important; }
  body{ background:#fff !important; }
  .page-wrap > *{ border:none; box-shadow:none; }
}
